import apiClient from '../plugins/Axios';

const apiGetAirplane = function () {
    return apiClient.get('http://localhost:8090/api/v1/airPlane/');
}
const apiSetAirplane = function (item) {
    return apiClient.post('http://localhost:8090/api/v1/airPlane/', item);
}
const apiUpdateAirplaneService = function (item) {
    return apiClient.post('http://localhost:8090/api/v1/airPlane/', item);
}
const apiDeleteAirplaneService = function (item) {
    return apiClient.delete(`http://localhost:8090/api/v1/airPlane/${item}`);
}
export default {
    apiGetAirplane,
    apiSetAirplane,
    apiUpdateAirplaneService,
    apiDeleteAirplaneService,

}