import apiClient from '../plugins/Axios';

const apiGetRadar = function () {
    return apiClient.get('http://localhost:8090/api/v1/radar/');
}
const apiSetRadar = function (item) {
    return apiClient.post('http://localhost:8090/api/v1/radar/', item);
}
const apiUpdateRadarService = function (item) {
    return apiClient.post('http://localhost:8090/api/v1/radar/', item);
}
const apiDeleteRadarService = function (item) {
    return apiClient.delete(`http://localhost:8090/api/v1/radar/${item}`);
}
export default {
    apiGetRadar,
    apiSetRadar,
    apiUpdateRadarService,
    apiDeleteRadarService,

}