import SquadronService from "@/service/SquadronService";

const actions = {
    async getSquadronState({ commit }) {
        try {
            const { data } = await SquadronService.apiGetSquadron();
            commit('SET_DATA_SQUADRON', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setSquadronStatenew({ commit }, item) {
        try {
            const { data } = await SquadronService.apiSetSquadron(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateSquadron({ commit }, item) {
        try {
            const { data } = await SquadronService.apiUpdateSquadronService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteSquadron({ commit }, item) {
        try {
            const { data } = await SquadronService.apiDeleteSquadronService(item);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;