import apiClient from '../plugins/Axios';

const apiGetAirPlaneType = function () {
    return apiClient.get('http://localhost:8090/api/v1/airPlaneType/');
}
const apiSetAirPlaneType = function (item) {
    return apiClient.post('http://localhost:8090/api/v1/airPlaneType/', item);
}
const apiUpdateAirPlaneTypeService = function (item) {
    return apiClient.put('http://localhost:8090/api/v1/airPlaneType/', item);
}
const apiDeleteAirPlaneTypeService = function (id) {
    return apiClient.delete(`http://localhost:8090/api/v1/airPlaneType/${id}`);
}
export default {
    apiGetAirPlaneType,
    apiSetAirPlaneType,
    apiUpdateAirPlaneTypeService,
    apiDeleteAirPlaneTypeService,

}