import makerService from "@/service/MakerService";

const actions = {
    async getMakerState({ commit }) {
        try {
            const { data } = await makerService.apiGetMakerService();
            commit('SET_DATA_Maker', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setMakernew({ commit }, item) {
        try {
            const { data } = await makerService.apiSetMakerService(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateMaker({ commit }, item) {
        try {
            const { data } = await makerService.apiUpdateMakerService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteMaker({ commit }, id) {
        console.log('acciones state', id)
        try {
            const { data } = await makerService.apiDeleteMakerService(id);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;