import radarTypeService from "@/service/RadarTypeService";

const actions = {
    async getRadarTypeState({ commit }) {
        try {
            const { data } = await radarTypeService.apiGetRadarTypeService();
            commit('SET_DATA_RadarType', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setRadarTypenew({ commit }, item) {
        try {
            const { data } = await radarTypeService.apiSetRadarTypeService(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateRadarType({ commit }, item) {
        try {
            const { data } = await radarTypeService.apiUpdateRadarTypeService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteRadarType({ commit }, id) {
        console.log('acciones state', id)
        try {
            const { data } = await radarTypeService.apiDeleteRadarTypeService(id);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;