import RadarService from "@/service/RadarService";

const actions = {
    async getRadarState({ commit }) {
        try {
            const { data } = await RadarService.apiGetRadar();
            commit('SET_DATA_RADAR', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setRadarStatenew({ commit }, item) {
        try {
            const { data } = await RadarService.apiSetRadar(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateRadar({ commit }, item) {
        try {
            const { data } = await RadarService.apiUpdateRadarService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteRadar({ commit }, item) {
        try {
            const { data } = await RadarService.apiDeleteRadarService(item);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;