import insuranceCarrierService from "@/service/InsuranceCarrierService";

const actions = {
    async getInsuranceCarrierState({ commit }) {
        try {
            const { data } = await insuranceCarrierService.apiGetInsuranceCarrierService();
            commit('SET_DATA_InsuranceCarrier', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setInsuranceCarriernew({ commit }, item) {
        try {
            const { data } = await insuranceCarrierService.apiSetInsuranceCarrierService(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateInsuranceCarrier({ commit }, item) {
        try {
            const { data } = await insuranceCarrierService.apiUpdateInsuranceCarrierService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteInsuranceCarrier({ commit }, id) {
        console.log('acciones state', id)
        try {
            const { data } = await insuranceCarrierService.apiDeleteInsuranceCarrierService(id);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;