import subComponentService from "@/service/SubComponentService";

const actions = {
    async getSubComponentState({ commit }) {
        try {
            const { data } = await subComponentService.apiGetSubComponentService();
            commit('SET_DATA_SubComponent', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setSubComponentnew({ commit }, item) {
        try {
            const { data } = await subComponentService.apiSetSubComponentService(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateSubComponent({ commit }, item) {
        try {
            const { data } = await subComponentService.apiUpdateSubComponentService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteSubComponent({ commit }, id) {
        console.log('acciones state', id)
        try {
            const { data } = await subComponentService.apiDeleteSubComponentService(id);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;