import apiClient from '../plugins/Axios';

const apiGetRoles = function () {
    return apiClient.get('http://localhost:8090/api/v1/roles/');
}
const apiSetRoles = function (item) {
    return apiClient.post('http://localhost:8090/api/v1/roles/', item);
}
const apiUpdateRolesService = function (item) {
    return apiClient.post('http://localhost:8090/api/v1/roles/', item);
}
const apiDeleteRolesService = function (item) {
    return apiClient.delete(`http://localhost:8090/api/v1/roles/${item}`);
}
export default {
    apiGetRoles,
    apiSetRoles,
    apiUpdateRolesService,
    apiDeleteRolesService,

}