import apiClient from '../plugins/Axios';

const uriServer = 'http://localhost:8090'

const apiGetRadarTypeService = function () {
    return apiClient.get(`${uriServer}/api/v1/radarType/`);
}
const apiSetRadarTypeService = function (item) {
    return apiClient.post(`${uriServer}/api/v1/radarType/`, item);
}
const apiUpdateRadarTypeService = function (item) {
    return apiClient.put(`${uriServer}/api/v1/radarType/`, item);
}
const apiDeleteRadarTypeService = function (id) {
    return apiClient.delete(`${uriServer}/api/v1/radarType/${id}`);
}
export default {
    apiGetRadarTypeService,
    apiSetRadarTypeService,
    apiUpdateRadarTypeService,
    apiDeleteRadarTypeService
}