import apiClient from '../plugins/Axios';

const uriServer = 'http://localhost:8090'

const apiGetMakerService = function () {
    return apiClient.get(`${uriServer}/api/v1/manufacturer/`);
}
const apiSetMakerService = function (item) {
    return apiClient.post(`${uriServer}/api/v1/manufacturer/`, item);
}
const apiUpdateMakerService = function (item) {
    return apiClient.put(`${uriServer}/api/v1/manufacturer/`, item);
}
const apiDeleteMakerService = function (id) {
    return apiClient.delete(`${uriServer}/api/v1/manufacturer/${id}`);
}
export default {
    apiGetMakerService,
    apiSetMakerService,
    apiUpdateMakerService,
    apiDeleteMakerService
}
