import ComponentAirplaneService from "@/service/ComponentAirplaneService";

const actions = {
    async getComponentAirplaneState({ commit }) {
        try {
            const { data } = await ComponentAirplaneService.apiGetComponentAirplaneService();
            commit('SET_DATA_ComponentAirplane', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setComponentAirplanenew({ commit }, item) {
        try {
            const { data } = await ComponentAirplaneService.apiSetComponentAirplaneService(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateComponentAirplane({ commit }, item) {
        try {
            const { data } = await ComponentAirplaneService.apiUpdateComponentAirplaneService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteComponentAirplane({ commit }, id) {
        console.log('acciones state', id)
        try {
            const { data } = await ComponentAirplaneService.apiDeleteComponentAirplaneService(id);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;