import apiClient from '../plugins/Axios';

const apiGetSubComponentService = function () {
    return apiClient.get('http://localhost:8090/api/v1/subComponent/');
}
const apiSetSubComponentService = function (item) {
    return apiClient.post('http://localhost:8090/api/v1/subComponent/', item);
}
const apiUpdateSubComponentService = function (item) {
    return apiClient.put('http://localhost:8090/api/v1/subComponent/', item);
}
const apiDeleteSubComponentService = function (id) {
    return apiClient.delete(`http://localhost:8090/api/v1/subComponent/${id}`);
}
export default {
    apiGetSubComponentService,
    apiSetSubComponentService,
    apiUpdateSubComponentService,
    apiDeleteSubComponentService,

}