import AirPlaneType from "@/service/AirPlaneType";

const actions = {
    async getAirPlaneTypeState({ commit }) {
        try {
            const { data } = await AirPlaneType.apiGetAirPlaneType();
            commit('SET_DATA_AirPlaneType', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setAirPlaneTypStatenew({ commit }, item) {
        try {
            const { data } = await AirPlaneType.apiSetAirPlaneType(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateAirPlaneType({ commit }, item) {
        try {
            const { data } = await AirPlaneType.apiUpdateAirPlaneTypeService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteAirPlaneType({ commit }, id) {
        console.log('acciones state', id)
        try {
            const { data } = await AirPlaneType.apiDeleteAirPlaneTypeService(id);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;