import AirPlaneService from "@/service/AirPlaneService";

const actions = {
    async getAirPlaneState({ commit }) {
        try {
            const { data } = await AirPlaneService.apiGetAirplane();
            commit('SET_DATA_AIRPLANE', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setAirPlaneStatenew({ commit }, item) {
        try {
            const { data } = await AirPlaneService.apiSetAirplane(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateAirplane({ commit }, item) {
        try {
            const { data } = await AirPlaneService.apiUpdateAirplaneService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteAirplane({ commit }, item) {
        try {
            const { data } = await AirPlaneService.apiDeleteAirplaneService(item);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;