import apiClient from '../plugins/Axios';

const uriServer = 'http://localhost:8090'

const apiGetSureTypeService = function () {
    return apiClient.get(`${uriServer}/api/v1/sureType/`);
}
const apiSetSureTypeService = function (item) {
    return apiClient.post(`${uriServer}/api/v1/sureType/`, item);
}
const apiUpdateSureTypeService = function (item) {
    return apiClient.put(`${uriServer}/api/v1/sureType/`, item);
}
const apiDeleteSureTypeService = function (id) {
    return apiClient.delete(`${uriServer}/api/v1/sureType/${id}`);
}
export default {
    apiGetSureTypeService,
    apiSetSureTypeService,
    apiUpdateSureTypeService,
    apiDeleteSureTypeService
}