import apiClient from '../plugins/Axios';

const apiGetSquadron = function () {
    return apiClient.get('http://localhost:8090/api/v1/squadron/');
}
const apiSetSquadron = function (item) {
    return apiClient.post('http://localhost:8090/api/v1/squadron/', item);
}
const apiUpdateSquadronService = function (item) {
    return apiClient.put('http://localhost:8090/api/v1/squadron/', item);
}
const apiDeleteSquadronService = function (id) {
    return apiClient.delete(`http://localhost:8090/api/v1/squadron/${id}`);
}
export default {
    apiGetSquadron,
    apiSetSquadron,
    apiUpdateSquadronService,
    apiDeleteSquadronService,

}