import sureTypeService from "@/service/SureTypeService";

const actions = {
    async getSureTypeState({ commit }) {
        try {
            const { data } = await sureTypeService.apiGetSureTypeService();
            commit('SET_DATA_SureType', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setSureTypenew({ commit }, item) {
        try {
            const { data } = await sureTypeService.apiSetSureTypeService(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateSureType({ commit }, item) {
        try {
            const { data } = await sureTypeService.apiUpdateSureTypeService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteSureType({ commit }, id) {
        console.log('acciones state', id)
        try {
            const { data } = await sureTypeService.apiDeleteSureTypeService(id);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;