import apiClient from '../plugins/Axios';

const apiGetComponentAirplaneService = function () {
    return apiClient.get('http://localhost:8090/api/v1/component/');
}
const apiSetComponentAirplaneService = function (item) {
    return apiClient.post('http://localhost:8090/api/v1/component/', item);
}
const apiUpdateComponentAirplaneService = function (item) {
    return apiClient.put('http://localhost:8090/api/v1/component/', item);
}
const apiDeleteComponentAirplaneService = function (id) {
    return apiClient.delete(`http://localhost:8090/api/v1/component/${id}`);
}
export default {
    apiGetComponentAirplaneService,
    apiSetComponentAirplaneService,
    apiUpdateComponentAirplaneService,
    apiDeleteComponentAirplaneService,

}