import SureService from "@/service/SureService";

const actions = {
    async getSureState({ commit }) {
        try {
            const { data } = await SureService.apiGetSureService();
            commit('SET_DATA_Sure', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setSurenew({ commit }, item) {
        try {
            const { data } = await SureService.apiSetSureService(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateSure({ commit }, item) {
        try {
            const { data } = await SureService.apiUpdateSureService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteSure({ commit }, id) {
        console.log('acciones state', id)
        try {
            const { data } = await SureService.apiDeleteSureService(id);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;
