import RolesService from "@/service/RolesService";

const actions = {
    async getRolesState({ commit }) {
        try {
            const { data } = await RolesService.apiGetRoles();
            commit('SET_DATA_ROLES', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setRolesStatenew({ commit }, item) {
        try {
            const { data } = await RolesService.apiSetRoles(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateRoles({ commit }, item) {
        try {
            const { data } = await RolesService.apiUpdateRolesService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteRoles({ commit }, item) {
        try {
            const { data } = await RolesService.apiDeleteRolesService(item);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;