import apiClient from '../plugins/Axios';

const uriServer = 'http://localhost:8090'

const apiGetLocationService = function () {
    return apiClient.get(`${uriServer}/api/v1/location/`);
}
const apiSetLocationService = function (item) {
    return apiClient.post(`${uriServer}/api/v1/location/`, item);
}
const apiUpdateLocationService = function (item) {
    return apiClient.put(`${uriServer}/api/v1/location/`, item);
}
const apiDeleteLocationService = function (id) {
    return apiClient.delete(`${uriServer}/api/v1/location/${id}`);
}
export default {
    apiGetLocationService,
    apiSetLocationService,
    apiUpdateLocationService,
    apiDeleteLocationService
}