import apiClient from '../plugins/Axios';

const uriServer = 'http://localhost:8090'

const apiGetSureService = function () {
    return apiClient.get(`${uriServer}/api/v1/sure/`);
}
const apiSetSureService = function (item) {
    return apiClient.post(`${uriServer}/api/v1/sure/`, item);
}
const apiUpdateSureService = function (item) {
    return apiClient.put(`${uriServer}/api/v1/sure/`, item);
}
const apiDeleteSureService = function (id) {
    return apiClient.delete(`${uriServer}/api/v1/sure/${id}`);
}
export default {
    apiGetSureService,
    apiSetSureService,
    apiUpdateSureService,
    apiDeleteSureService
}
