import apiClient from '../plugins/Axios';

const urlServer = 'http://localhost:8090'

const apiGetInsuranceCarrierService = function () {
    return apiClient.get(`${urlServer}/api/v1/insuranceCarrier/`);
}
const apiSetInsuranceCarrierService = function (item) {
    return apiClient.post(`${urlServer}/api/v1/insuranceCarrier/`, item);
}
const apiUpdateInsuranceCarrierService = function (item) {
    return apiClient.put(`${urlServer}/api/v1/insuranceCarrier/`, item);
}
const apiDeleteInsuranceCarrierService = function (id) {
    return apiClient.delete(`${urlServer}/api/v1/insuranceCarrier/${id}`);
}
export default {
    apiGetInsuranceCarrierService,
    apiSetInsuranceCarrierService,
    apiUpdateInsuranceCarrierService,
    apiDeleteInsuranceCarrierService
}