import locationService from "@/service/LocationService";

const actions = {
    async getLocationState({ commit }) {
        try {
            const { data } = await locationService.apiGetLocationService();
            commit('SET_DATA_Location', data)
        } catch (error) {
            console.log(error);
        }
    },
    async setLocationnew({ commit }, item) {
        try {
            const { data } = await locationService.apiSetLocationService(item);
            console.log('agregar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiUpdateLocation({ commit }, item) {
        try {
            const { data } = await locationService.apiUpdateLocationService(item);
            console.log('actualizar',data)
        } catch (error) {
            console.log(error);
        }
    },
    async apiDeleteLocation({ commit }, id) {
        console.log('acciones state', id)
        try {
            const { data } = await locationService.apiDeleteLocationService(id);
            console.log('Eliminado',data)
        } catch (error) {
            console.log(error);
        }
    },
}
export default actions;